.nav {
  position: sticky;
  top: 0;
  background-color: black;
  color: white;
  padding: 8px;
  padding-left: 15px;
  font-family: "Geist Mono", serif;
  display: flex;
  z-index: 15;
}

.nav-title {
  flex: 1;
}

.dev {
  position: fixed;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  z-index: 20;
  display: flex;
  flex-direction: column;
  align-items: center;
  cursor: pointer;

  /* transform: translate(0, -50px); */
}

.dev-button {
  height: 19px;
  background-color: rgb(239, 217, 23);
  padding: 3px 20px;
  border-radius: 10px 0 0 10px;
  align-self: flex-end;
}

.dev-content {
  border: 4px solid rgb(239, 217, 23);
  height: 500px;
  width: 150px;
  border-radius: 20px 0 0 20px;
  border-right: none;
  background-color: rgb(255, 255, 255);
  padding: 10px;

  opacity: 0;
  transform: translateX(100%);
  transition: opacity 0.5s ease, transform 0.5s ease;
}

/* Active state: visible */
.dev-content.active {
  opacity: 1;
  transform: translateX(0);
}
